textarea {
  width: 100%;
  height: 200px;
  resize: none;
  padding: 5px;
  border: 1px solid #e0e0e0;
  outline: none;
}
.operator {
  text-align: right;
}
.feedback-type-select {
  width: 160px;
  text-align: left;
}
.input {
  width: 240px;
  height: 26px;
  border: none;
  border-bottom: 1px solid #ccc;
  outline: none;
  padding: 0 5px;
  color: #666;
}
.input:focus {
  border-bottom-color: #3367D6;
}
.input::placeholder {
  text-align: center;
  color: #ccc;
  font-size: 12px;
}
.button-group {
  text-align: right;
  margin-top: 20px;
}
.button-group .btn {
  width: 80px;
  height: 36px;
  margin-left: 16px;
  font-size: 14px;
  border-radius: 3px;
  border: none;
  box-shadow: 1px 1px 3px #ccc;
  outline: none;
  transition: all .5s;
  cursor: pointer;
}
.button-group .btn:active {
  filter: opacity(0.6);
}
.btn.submit {
  color: #fff;
  background: #3367D6;
}
