@import '~antd/dist/antd.css';
* {
  margin: 0;
  padding: 0;
}
body {
  background: #F8F9FA;
  font-size: 16px;
}
.link {
  text-decoration: none;
}
.options-page {
  width: 100%;
}
.header {
  width: 100%;
  height: 56px;
  line-height: 56px;
  font-weight: bold;
  text-indent: 25px;
  background: #3367D6;
  color: #fff;
}
.content {
  margin-top: 10px;
  overflow: hidden;
}
.content .menus {
  width: 200px;
  margin-left: 25px;
  float: left;
}
.content .menus .menu {
  list-style-type: none;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  color: #444;
}
.content .menus .menu .title {
  margin-left: 10px;
}
.content .menus .menu.last {
  border-top: 1px solid #ccc;
  margin-top: 10px;
}
.content .container {
  margin: 10px auto;
  width: 700px;
}
.content .container .label {
  margin: 20px 0 10px;
  font-size: 13px;
  font-weight: bold;
  color: #666;
}
.content .container .block {
  background: #fff;
  box-shadow: 1px 1px 5px #ccc;
}
.block .options-list .option-item {
  padding: 15px 20px;
  border-bottom: 1px solid #eee;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #333;
}
.block .options-list .option-item .desc {
  font-size: 12px;
  margin-top: 5px;
  color: #999;
}
.block .options-list .option-item .main {
  width: 100%;
  float: left;
}
.block .options-list .option-item .arraw {
  float: right;
  text-align: right;
}
.footer {
  font-size: 12px;
  color: #bbb;
  text-align: center;
  margin: 30px 0 10px;
}
.footer a {
  color: #3367D6;
}